@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;800;900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Arvo);
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
p {
font-size: 18px;
font-family: 'Montserrat', sans-serif!important;
font-weight: 300;
}
h1{
  font-size: 40px!important;
  font-family: 'Montserrat', sans-serif!important;
  font-weight: 800!important;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


body,
html {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background: #f5f4f4;
  font-size: 13px;
  font-family: "Arvo", monospace;
}
@supports (display: grid) {
  body,
html {
    display: block;
  }
}

.message {
  border: 1px solid #d2d0d0;
  padding: 2em;
  font-size: 1.7vw;
  box-shadow: -2px 2px 10px 0px rgba(68, 68, 68, 0.4);
}
@supports (display: grid) {
  .message {
    display: none;
  }
}

.section {
  display: none;
  padding: 2rem;
}
@media screen and (min-width: 768px) {
  .section {
    padding: 4rem;
  }
}
@supports (display: grid) {
  .section {
    display: block;
  }
}

h1 {
  font-size: 2rem;
  margin: 0 0 1.5em;
}

.grid {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-auto-rows: 150px;
  grid-auto-flow: row dense;
  margin-bottom: 50px;
}

.item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  box-sizing: border-box;
  background: #0c9a9a;
  color: #fff;
  grid-column-start: auto;
  grid-row-start: auto;
  color: #fff;
  background: url("/work/static/media/epiphanyai [Converted].60448efa.svg");
  background-size: cover;
  background-position: center;
  box-shadow: -2px 2px 10px 0px rgba(68, 68, 68, 0.4);
  transition: transform 0.3s ease-in-out;
  cursor: pointer;
  counter-increment: item-counter;
}
.item:nth-of-type(2n) {
    background-image: url("/work/static/media/fridaymeeting ft the chosen 3 .856c58d9.jpg");
  }
.item:nth-of-type(3n) {
  background-image: url("/work/static/media/lifting holy hands4.980e0815.png");
}
.item:nth-of-type(4n) {
  background-image: url("/work/static/media/january schedule copy.46808dd9.jpg");
}
.item:nth-of-type(5n) {
  background-image: url(/work/static/media/prayermeetingAE4.723abcc9.jpg);
}
.item:nth-of-type(6n) {
  background-image: url(/work/static/media/AF5E50EC-A81A-4E02-9E1D-1AA322A1797E.bed96dd1.PNG);
}
.item:nth-of-type(7n) {
    background-image: url("/work/static/media/St Mark Logo graph.445a1de2.png");
}
.item:nth-of-type(8n) {
    background-image: url("/work/static/media/worship ad2.9510b0f2.jpg");
}
.item:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.1;
  transition: opacity 0.1s ease-in-out;
}
.item:hover {
  transform: scale(1.05);
}
.item:hover:after {
  opacity: 0;
}
.item--medium {
  grid-row-end: span 3;
}
.item--large {
  grid-row-end: span 5;
}
.item--full {
  grid-column-end: auto;
}
@media screen and (min-width: 768px) {
  .item--full {
    grid-column: 1/-1;
    grid-row-end: span 2;
  }
}
.item__details {
  position: relative;
  z-index: 1;
  padding: 15px;
  color: #444;
  background: #fff;
  text-transform: lowercase;
  letter-spacing: 1px;
  color: #828282;
}
.item__details:before {
  content: counter(item-counter);
  font-weight: bold;
  font-size: 1.1rem;
  padding-right: 0.5em;
  color: #444;
}
